import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: () => import('pages/HomePage')
    },
    {
      path: '/news',
      name: 'news',
      component: () => import('pages/NewsPage')
    }
  ]
})

export default router